import { FunctionComponent } from 'preact';
import { classes } from '@adobe-commerce/elsie/lib';
import '@/b2b-purchase-order/components/PurchaseOrdersTableActions/PurchaseOrdersTableActions.css';
import { Button } from '@adobe-commerce/elsie/components';
import { PurchaseOrdersTableActionsProps } from '@/b2b-purchase-order/types/components';

export const PurchaseOrdersTableActions: FunctionComponent<
  PurchaseOrdersTableActionsProps
> = ({
  loading,
  selectedCount,
  className,
  handleRejectSelected,
  handleApproveSelected,
}) => {
  return (
    <div
      className={classes([
        'b2b-purchase-order-purchase-orders-table-actions',
        className,
      ])}
    >
      <div>Selected {selectedCount}</div>
      <div
        className={'b2b-purchase-order-purchase-orders-table-actions__buttons'}
      >
        <Button
          variant="secondary"
          onClick={handleRejectSelected}
          disabled={loading || selectedCount === 0}
        >
          Reject selected
        </Button>
        <Button
          variant="primary"
          onClick={handleApproveSelected}
          disabled={loading || selectedCount === 0}
        >
          Approve selected
        </Button>
      </div>
    </div>
  );
};
